import {
    Streamlit,
    StreamlitComponentBase,
    withStreamlitConnection,
    ComponentProps
} from "streamlit-component-lib"
import React, { useEffect, useMemo, useState } from "react"
import useSessionStorageState from 'use-session-storage-state'

const BrowserSessionStorageComponent: React.FC<ComponentProps> = (props: any) => {

    const { args } = props
    const method: any = args["method"]
    const itemKey: any = args["itemKey"]
    const itemValue: any = args["itemValue"]
    const sessionStrageOptions: any = args["sessionStrageOptions"] || { defaultValue: null }
    const [storageItems, setStorageItems, { removeItem, isPersistent }] = useSessionStorageState<any>(itemKey, sessionStrageOptions)

    const setItemF = (itemKey: string, itemValue: any) => {
        if (itemValue !== undefined) {
            let toSave = { [itemKey]: itemValue }
            setStorageItems(toSave)
        }
    }

    const deleteAll = () => {
        sessionStorage.clear();
        return true
    }

    const parseString = (str: any) => {
        try {
            return JSON.parse(str);
        } catch (e) {
            return null;
        }
    }

    const [allSessionStorage, setAllSessionStorage] = useState<any>({})

    const getAll = () => {
        const allKeys = Object.keys(sessionStorage);
        const results = { ...sessionStorage }

        allKeys.map((key: any) => {
            if (parseString(results[key]) !== null) {
                const res: any = parseString(results[key])
                if (res !== null) {
                    allSessionStorage[key] = res[key]
                }
            }
        })

        setAllSessionStorage(allSessionStorage)
        return allSessionStorage
    }

    const eraseItem = (itemKey: any) => {
        sessionStorage.removeItem(itemKey)
    }

    useEffect(() => {

        const method: any = args["method"]
        const itemKey: any = args["itemKey"]
        const itemValue: any = args["itemValue"]

        Streamlit.setFrameHeight(0)

        switch (method) {
            case "getItem":
                let retVal = null;
                if (storageItems && storageItems[itemKey]) {
                    retVal = storageItems[itemKey]
                } else {
                    const allStored = getAll();
                    if (allStored && allStored[itemKey]) {
                        retVal = allStored[itemKey]
                    }
                }
                Streamlit.setComponentValue(retVal)
                Streamlit.setComponentReady()

                break
            case "setItem":

                setItemF(itemKey, itemValue)
                break
            case "deleteItem":

                removeItem()
                break
            case "eraseItem":
                eraseItem(itemKey)
                break

            case "deleteAll":
                deleteAll()
                break

            case "getAll":

                const allStored = getAll()
                Streamlit.setComponentValue(allStored)
                Streamlit.setComponentReady()
                break

            default:
                console.error(method + " is not recognized")
                break
        }


    }, [method, itemKey, itemValue])


    return (
        <div style={{ display: "none" }} >
        </div>
    )
}

export default withStreamlitConnection(BrowserSessionStorageComponent)


